import fuzz from 'fuzzball';
import replace from 'lodash/replace';

const OPTIONS = {
  full_process: false,
};

const PATTERN = new RegExp(/\(\)\./, 'g');
const process = (str) => replace(str.toLowerCase(), PATTERN, ' ');

function getRatios(string1, string2) {
  const str1 = process(string1);
  const str2 = process(string2);
  const sr = fuzz.ratio(str1, str2, OPTIONS);
  const pr = fuzz.partial_ratio(str1, str2, OPTIONS);
  const tst = fuzz.token_set_ratio(str1, str2, OPTIONS);
  const tsr = fuzz.token_sort_ratio(str1, str2, OPTIONS);
  const ptsr = fuzz.partial_token_sort_ratio(str1, str2, OPTIONS);
  const wr = fuzz.WRatio(str1, str2, OPTIONS);
  const d = fuzz.distance(str1, str2, OPTIONS);

  return { sr, pr, tst, tsr, ptsr, wr, d };
};

export default getRatios;



