import React, { Component } from 'react';
import moment from 'moment';
import genTeamTuples from './genTeamTuples';
import getCombinations from './getCombinations';
import {
  getRSEvent,
  getSSEvent,
} from './getSelectionList';
import matchEvents from './matchEvents';

const SS_TEAM_TUPLES = genTeamTuples(false);
const RS_TEAM_TUPLES = genTeamTuples(true);
const TEAM_TUPLES_SIZE = SS_TEAM_TUPLES.length;
const POOL_SIZE = 20;
const EVENT_SIZE = 15;

const getRSE = (tupleIndex, Pool) => {
  const teamTuple = RS_TEAM_TUPLES[tupleIndex];
  const poolIndex = Pool.indexOf(tupleIndex);
  return getRSEvent(teamTuple, { poolIndex, tupleIndex });
};

const getSSE = (tupleIndex, Pool) => {
  const teamTuple = SS_TEAM_TUPLES[tupleIndex];
  const poolIndex = Pool.indexOf(tupleIndex);
  return getSSEvent(teamTuple, { poolIndex, tupleIndex });
};

const commonRowStyle = {
  background: 'greenyellow',
};

const uniqRowStyle = {
  background: 'gold',
};

const cellStyle = {
  width: '500px',
};

const getExpectEventTable = (Pool, SSEventIds, RSEventIds) => {
  const CommonIds = RSEventIds.reduce((ids, id) =>
    SSEventIds.includes(id) ? [...ids, id] : ids, []);
  const RSUniqIds = RSEventIds.filter(id => !CommonIds.includes(id));
  const SSUniqIds = SSEventIds.filter(id => !CommonIds.includes(id));
  const CommonParts = CommonIds.map(id => {
    const RSE = getRSE(id, Pool);
    const SSE = getSSE(id, Pool);
    return (
      <tr key={id} style={commonRowStyle}>
        <td style={cellStyle}>{RSE.FixtureName} / {moment(RSE.StartTime).format('MMM DD hh:mm')}</td>
        <td style={cellStyle}>{SSE.FixtureName} / {moment(SSE.StartTime).format('MMM DD hh:mm')}</td>
      </tr>
    );
  });
  const RemainParts = RSUniqIds.map((rsId, i) => {
    const RSE = getRSE(rsId, Pool);
    const ssId = SSUniqIds[i];
    const SSE = getSSE(ssId, Pool);
    return (
      <tr key={`${rsId}_${ssId}`} style={uniqRowStyle}>
        <td style={cellStyle}>{RSE.FixtureName} / {moment(RSE.StartTime).format('MMM DD hh:mm')}</td>
        <td style={cellStyle}>{SSE.FixtureName} / {moment(SSE.StartTime).format('MMM DD hh:mm')}</td>
      </tr>
    );
  });
  return (
    <table>
      <thead><tr><th>Expect:</th><th>{CommonIds.length} Matches</th></tr></thead>
      <tbody>
        {CommonParts}
        {RemainParts}
      </tbody>
    </table>
  );
};

const getActualEventTable = (Actual) => {
  const Matched = Actual.filter(event => event.matched !== undefined);
  const CommonParts = Matched.map(event1 => {
    const event2 = event1.matched;
    return (
      <tr key={event1.FixtureName} style={commonRowStyle}>
        <td style={cellStyle}>{event1.FixtureName} / {moment(event1.StartTime).format('MMM DD hh:mm')}</td>
        <td style={cellStyle}>{event2.FixtureName} / {moment(event2.StartTime).format('MMM DD hh:mm')}</td>
      </tr>
    );
  });
  const NotMatched = Actual.filter(event => event.matched === undefined);
  const RSType = NotMatched.filter(event => event.FixtureName.includes('vs'));
  const SSType = NotMatched.filter(event => event.FixtureName.includes('@'));
  console.log('Length should be the same', RSType.length === SSType.length);
  const RemainParts = RSType.map((event1, i) => {
    const event2 = SSType[i];
    return (
      <tr key={`${event1.FixtureName}_${event2.FixtureName}`} style={uniqRowStyle}>
        <td style={cellStyle}>{event1.FixtureName} / {moment(event1.StartTime).format('MMM DD hh:mm')}</td>
        <td style={cellStyle}>{event2.FixtureName} / {moment(event2.StartTime).format('MMM DD hh:mm')}</td>
      </tr>
    );
  });
  return (
    <table>
      <thead><tr><th>Actual:</th><th>{Matched.length} Matches</th></tr></thead>
      <tbody>
        {CommonParts}
        {RemainParts}
      </tbody>
    </table>
  );
};

class DisplayDatas extends Component {
  constructor() {
    super();

    this.state = {
      pool: [],
      RSEventIds: [],
      SSEventIds: [],
      SSEvents: [],
      RSEvents: [],
      actualEvents: [],
    };
  }

  render() {
    const Pool = this.state.pool;

    const SSEventIds = this.state.SSEventIds;
    const RSEventIds = this.state.RSEventIds;

    const SSEvents = this.state.SSEvents;
    const RSEvents = this.state.RSEvents;

    const Actual = this.state.actualEvents;

    const expectEventTable = getExpectEventTable(Pool, SSEventIds, RSEventIds);
    const actualEventTable = getActualEventTable(Actual);

    return (
      <div>
        <button onClick={() => {
          const pool = getCombinations(TEAM_TUPLES_SIZE, POOL_SIZE);
          this.setState({
            ...this.state,
            pool,
            RSEventIds: [],
            SSEventIds: [],
            SSEvents: [],
            RSEvents: [],
            actualEvents: [],
          });
        }}>
          Get Permutation Ids
        </button>
        <button onClick={() => {
          if (Pool.length !== POOL_SIZE) return;
          const RS = getCombinations(POOL_SIZE, EVENT_SIZE);
          const SS = getCombinations(POOL_SIZE, EVENT_SIZE);
          const RSEventIds = RS.map(v => Pool[v]);
          const SSEventIds = SS.map(v => Pool[v]);

          const RSEvents = RSEventIds.map(tupleIndex => getRSE(tupleIndex, Pool));
          const SSEvents = SSEventIds.map(tupleIndex => getSSE(tupleIndex, Pool));

          this.setState({
            ...this.state,
            RSEventIds,
            SSEventIds,
            RSEvents,
            SSEvents,
            actualEvents: [],
          });
        }}>
          Generate RS and SS Events
        </button>
        <button onClick={() => {
          const actualEvents = matchEvents(RSEvents, SSEvents);
          this.setState(prevState => {
            return {
              ...prevState,
              actualEvents,
            };
          });
        }}>
          Match Events
        </button>
        <div>{`Team Name Permutations in NBA: ${TEAM_TUPLES_SIZE}, #Permutations to generate events: ${POOL_SIZE}, #Test Events for RS/SS: ${EVENT_SIZE}`}</div>
        <div>{Pool.reduce((s, v) => `${s}${v}, `, 'Permutaion Ids: ')}</div>
        {expectEventTable}
        {actualEventTable}
      </div>
    );
  }
}

export default DisplayDatas;



