import fuzz from 'fuzzball';
import replace from 'lodash/replace';
import getTimeScore from './getTimeScore';

const HIGHEST_LEVEL = 95;
const HIGH_LEVEL = 86;

const FUZZ_OPTIONS = {
  full_process: false,
  proc_sorted: true,
};

const PATTERN = new RegExp(/\(\)\./, 'g');

const stringProcessAndSort = (str) => {
  const normalised = replace(str.toLowerCase(), PATTERN, ' ');
  return fuzz.process_and_sort(normalised);
};

const getProcObj = (str) => {
  const procSorted = stringProcessAndSort(str);
  const tokens = fuzz.unique_tokens(procSorted);
  return {
    procSorted,
    tokens,
  };
};

const isTimeClose = (event1, event2) => {
  const timeScore = getTimeScore(event1.StartTime, event2.StartTime);
  return (timeScore >= HIGHEST_LEVEL);
};

const isTeamSimilar = (team1, team2) => {
  const procObj1 = getProcObj(team1.Name);
  const procObj2 = getProcObj(team2.Name);
  const options = {
    ...FUZZ_OPTIONS,
    tokens: [procObj1.tokens, procObj2.tokens],
  };
  const teamNameScore = fuzz.WRatio(procObj1.procSorted, procObj2.procSorted, options);
  return (teamNameScore >= HIGH_LEVEL);
};

const isParticipantsSimilar = (event1, event2) => {
  const P1 = event1.Participants;
  const P2 = event2.Participants;
  return P1.reduce((res, t1) => {
    const t2 = P2.find(t => t.Side[0] === t1.Side[0]);
    const teamSimilar = isTeamSimilar(t1, t2);
    return res && teamSimilar;
  }, true);
};

const mergeEvents = (event1, event2) => {
  return {
    ...event1,
    matched: event2,
  };
};

export default function matchEvents(events1, events2) {
  const remains = [...events2];
  const matchResult = events1.reduce((res, e1) => {
    const timeCloseEvents = remains.filter(e2 => isTimeClose(e1, e2));
    if (timeCloseEvents.length === 0) {
      console.log('Could not match', e1.FixtureName);
      return [...res, e1];
    }

    const similarParticipants = timeCloseEvents.filter(e2 => isParticipantsSimilar(e1, e2));
    if (similarParticipants.length > 1) {
      console.log('Special case', e1, similarParticipants);
      return [...res, e1];
    }
    if (similarParticipants.length === 1) {
      const similarEvent = similarParticipants[0];
      const mergedEvent = mergeEvents(e1, similarEvent);
      const idx = remains.findIndex(r => r.FixtureName === similarEvent.FixtureName);
      remains.splice(idx, 1);
      console.log('Match by team name', `${e1.FixtureName} | ${similarEvent.FixtureName}`, idx);
      return [...res, mergedEvent];
    }

    console.log(e1.FixtureName, 'Not match w/ candidate', timeCloseEvents);
    return [...res, e1];
  }, []);
  if (remains.length > 0) {
    return [...matchResult, ...remains];
  }
  return matchResult;
}



