import TeamNameTuples from './data/NBA_Team_Names.json';

function genTeamTuples(key = false) {
  return TeamNameTuples.reduce((arr, teamTupleA) => {
    const teamTupleBList = TeamNameTuples
      .filter(teamNameTuple => teamNameTuple[0] !== teamTupleA[0]);
    const tuples = teamTupleBList.map(teamTupleB => {
      if (key) {
        return [teamTupleA[0], teamTupleB[0]];
      }
      return [teamTupleA[1], teamTupleB[1]];
    });
    return [...arr, ...tuples];
  }, []);
}

export default genTeamTuples;



